import share from '../common/share.js';

var _self;
export default {
	data() {
		return {
			TabCur: 0,
			scrollLeft: 0,
			tab: ['馆藏', '书评'],
			switchA: false,
			modalName: false,
			bookId:'',
			resBookDetail:'',
			comment:[]
		};
	},
	onLoad(params) {
		_self = this;
		uni.showLoading({
			title:'正在玩命加载'
		});
		this.fenxiang();
		this.bookId = params.bookId;
		console.log(this.bookId);

		uni.setNavigationBarColor({
			frontColor:'#000000',
			backgroundColor: '#FFFFFF'
		});
		this.refresh();

	},
	// 监听原生导航栏
	onNavigationBarButtonTap(e) {
		var index = e.index;
		if (index == 0) {
			this.shareInfo();
		}
	},
	onPullDownRefresh() {
		_self.refresh()
	},
	onShareAppMessage(res) {
		if (res.from === 'button') {
			// 来自页面内分享按钮
			console.log(res.target);
		}
		return {
			title: '自定义分享标题',
			path: '/pages/test/test?id=123'
		};
	},
	methods: {
		refresh(){
			this.getBookDetail(this.bookId);
		},
		tabSelect(index) {
			this.TabCur = index;
            console.log(this.TabCur);
            this.scrollLeft = (index - 1) * 60;
		},
		SwitchA(e) {
			this.switchA = !this.switchA;
			for (let i = 0; i < this.list.length; i++) {
				this.list[i].check = this.switchA;
			}
		},
		//设置该小说
		jumpPage(url){
			if(url && typeof(url)=='string'){
				uni.navigateTo({
					url: url,
					animationType: 'zoom-out'
				});
			}
		},
		getBookDetail(bookId) {
			uni.request({
				url: this.serverUrl + '/book/getBookById?bookId='+bookId,
				success: res => {
					this.resBookDetail = res.data.data;
					console.log(res.data.data);
					_self.requestGetBookComment()
				},
				complete(res) {
					uni.hideLoading();
				}
			});
		},
		addBook() {
			uni.request({
				method:"POST",
				data:{
					userId:_self.userId,
					bookId:_self.bookId
				},
				url: this.serverUrl + '/book/addSave',
				success: res => {
					_self.showModal()
				},
				complete(res) {
				}
			});
		},
		fenxiang() {
			uni.setTabBarItem({
				index: 0,
				text: 'text',
				iconPath: '/path/to/iconPath',
				selectedIconPath: '/path/to/selectedIconPath'
			});
		},
		showModal(e) {
			this.modalName = true;
			setTimeout(()=>{
				this.hideModal()
			},2000);
		},
		hideModal(e) {
			this.modalName = false;
		},
		requestGetBookComment() {
			uni.request({
				url:this.serverUrl + '/comment/getCommentByBookId?bookId=' + this.bookId,
				method:'GET',
				success(res) {
					_self.comment = res.data.data;
					console.log(_self.comment);
				},
				complete(res) {
					uni.stopPullDownRefresh();
					uni.hideLoading();
					uni.hideNavigationBarLoading()
				}
			})
		},
		shareInfo() {

			let shareInfo = {
				href: 'https://uniapp.dcloud.io',
				title: '分享测试',
				desc: '分享测试',
				imgUrl: '/static/logo.png'
			};
			let shareList = [
				{
					icon: '/static/sharemenu/wx.png',
					text: '微信好友'
				},
				{
					icon: '/static/sharemenu/pyq.png',
					text: '朋友圈'
				},
				{
					icon: '/static/sharemenu/weibo.png',
					text: '微博'
				},
				{
					icon: '/static/sharemenu/qq.png',
					text: 'QQ'
				},
				{
					icon: '/static/sharemenu/copy.png',
					text: '复制'
				},
				{
					icon: '/static/sharemenu/more.png',
					text: '更多'
				}
			];
			this.shareObj = share(shareInfo, shareList, function(index) {
				console.log('点击按钮的序号: ' + index);
				let shareObj = {
					href: shareInfo.href || '',
					title: shareInfo.title || '',
					summary: shareInfo.desc || '',
					success: res => {
						console.log('success:' + JSON.stringify(res));
					},
					fail: err => {
						console.log('fail:' + JSON.stringify(err));
					}
				};
				switch (index) {
					case 0:
						shareObj.provider = 'weixin';
						shareObj.scene = 'WXSceneSession';
						shareObj.type = 0;
						shareObj.imageUrl = shareInfo.imgUrl || '';
						uni.share(shareObj);
						break;
					case 1:
						shareObj.provider = 'weixin';
						shareObj.scene = 'WXSenceTimeline';
						shareObj.type = 0;
						shareObj.imageUrl = shareInfo.imgUrl || '';
						uni.share(shareObj);
						break;
					case 2:
						shareObj.provider = 'sinaweibo';
						shareObj.type = 0;
						shareObj.imageUrl = shareInfo.imgUrl || '';
						uni.share(shareObj);
						break;
					case 3:
						shareObj.provider = 'qq';
						shareObj.type = 1;
						shareObj.imageUrl = shareInfo.imgUrl || '';
						uni.share(shareObj);
						break;
					case 4:
						uni.setClipboardData({
							data: shareInfo.href,
							complete() {
								uni.showToast({
									title: '已复制到剪贴板'
								});
							}
						});
						break;
					case 5:

						break;
				}
			});
			this.$nextTick(() => {
				this.shareObj.alphaBg.show();
				this.shareObj.shareMenu.show();
			});
		}
	}
};
